
.nav {
    max-width: 968px;
    height: 3.5rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.nav__logo,
.nav__toggle {
    color: $title-color;
    font-weight: $medium;

}

.nav__logo:hover {
    color: $blue;
}

.nav__toggle {
    font-size: 2rem;
    cursor: pointer;

    &:hover{
        color: $blue;
    }
}


@include tablet {
    .nav__menu {
        position:fixed;
        bottom:-100%;
        left:0;
        width:100%;
        background-color: $body-color;
        padding: 2.5rem 2rem 4.5rem;
        box-shadow: 0 -1px 4px rgba(0,0,0,.15);
        border-radius: 2rem 2rem 0 0 ;
        transition: .3s;
    }
}

.nav__list{
    grid-template-columns: repeat(3, 1fr);
    column-gap: 2rem;
}

.nav__link {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 1.5rem;
    color: $title-color;
    font-weight: $medium;

    &:hover {
        color: $blue;
    }
}

.nav__icon {
    font-size: 2rem;

}

.nav__close {
    position: absolute;
    right: 2rem;
    bottom: 1.2rem;
    font-size: 2.4rem;
    cursor: pointer;
    color: $blue;

    &:hover{
        color:$blue-alt;
    }
}

.show-menu {
    bottom: 0;
}