html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  background-color: hsl(194deg, 60%, 99%);
  color: hsl(194deg, 8%, 45%);
}

.grid {
  display: grid;
  gap: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3, h4 {
  color: hsl(194deg, 8%, 65%);
  font-family: "Poppins", sans-serif;
  margin: 2.5rem;
  font-weight: 600;
  text-align: center;
}

ul {
  list-style: none;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.section {
  padding: 2.5rem 0 5rem;
}

.section__subtitle {
  display: block;
  margin-bottom: 3rem;
}

.header {
  width: 100%;
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 100;
  background-color: hsl(194deg, 60%, 99%);
}

.nav {
  max-width: 968px;
  height: 3.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav__logo,
.nav__toggle {
  color: hsl(194deg, 8%, 15%);
  font-weight: 500;
}

.nav__logo:hover {
  color: hsl(193deg, 69%, 61%);
}

.nav__toggle {
  font-size: 2rem;
  cursor: pointer;
}
.nav__toggle:hover {
  color: hsl(193deg, 69%, 61%);
}

@media (max-width: 768px) {
  .nav__menu {
    position: fixed;
    bottom: -100%;
    left: 0;
    width: 100%;
    background-color: hsl(194deg, 60%, 99%);
    padding: 2.5rem 2rem 4.5rem;
    box-shadow: 0 -1px 4px rgba(0, 0, 0, 0.15);
    border-radius: 2rem 2rem 0 0;
    transition: 0.3s;
  }
}
.nav__list {
  grid-template-columns: repeat(3, 1fr);
  column-gap: 2rem;
}

.nav__link {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 1.5rem;
  color: hsl(194deg, 8%, 15%);
  font-weight: 500;
}
.nav__link:hover {
  color: hsl(193deg, 69%, 61%);
}

.nav__icon {
  font-size: 2rem;
}

.nav__close {
  position: absolute;
  right: 2rem;
  bottom: 1.2rem;
  font-size: 2.4rem;
  cursor: pointer;
  color: hsl(193deg, 69%, 61%);
}
.nav__close:hover {
  color: hsl(194deg, 57%, 53%);
}

.show-menu {
  bottom: 0;
}

.home__container {
  gap: 1.5rem;
}

.home__content {
  grid-template-columns: 1fr 3fr;
  padding-top: 4rem;
  align-items: center;
}

.home__social {
  display: grid;
  grid-template-columns: max-content;
  row-gap: 1.5rem;
}
.home__social-icon {
  font-size: 2rem;
  color: hsl(193deg, 69%, 61%);
}
.home__social-icon:hover {
  color: hsl(194deg, 57%, 53%);
}

.home__blob {
  width: 20rem;
  fill: hsl(193deg, 69%, 61%);
}

.home__blob-img {
  width: 17rem;
}

.home__data {
  grid-column: 1/3;
}

.home__title {
  font-size: 4rem;
  color: hsl(194deg, 8%, 15%);
}

.home__subtitle {
  font-size: 3rem;
  color: hsl(194deg, 8%, 45%);
  font-weight: 500;
  margin-bottom: 1rem;
}

.home__description {
  margin-bottom: 2rem;
  font-size: 2rem;
}

.home__scroll {
  display: none;
}

.home__scroll-button {
  color: hsl(193deg, 69%, 61%);
  transition: 0.3s;
}
.home__scroll-button:hover {
  transform: translateY(1rem);
}

.home__scroll-mouse {
  font-size: 4rem;
}

.home__scroll-name {
  font-size: 1.5rem;
  color: hsl(194deg, 8%, 15%);
  font-weight: 500;
  margin-right: 0.5rem;
}

.home__scroll-arrow {
  font-size: 2rem;
}

.button {
  display: inline-block;
  background-color: hsl(193deg, 69%, 61%);
  color: #fff;
  padding: 1.75rem;
  border-radius: 1rem;
  font-weight: 500;
}
.button:hover {
  background-color: hsl(194deg, 57%, 53%);
}
.button__icon {
  font-size: 2rem;
  margin-left: 0.5rem;
  transition: 0.3s;
}

.button--flex {
  display: inline-flex;
  align-items: center;
}