.home__container {
    gap: 1.5rem;
}

.home__content {
    grid-template-columns: 1fr 3fr;
    padding-top: 4rem;
    align-items: center;
}

.home__social{
    display: grid;
    grid-template-columns: max-content;
    row-gap: 1.5rem;

    &-icon {
        font-size: 2rem;
        color: $blue;

        &:hover{
            color: $blue-alt;
        }
    }
}

.home__blob {
    width: 20rem;
    fill: $blue;
}

.home__blob-img {
    width: 17rem;
}

.home__data {
    grid-column: 1 /3;
}

.home__title {
    font-size: 4rem;
    color: $title-color;
}

.home__subtitle {
    font-size: 3rem;
    color: $text-color;
    font-weight: $medium;
    margin-bottom: 1rem;
}

.home__description {
    margin-bottom: 2rem;
    font-size: 2rem;
}

.home__scroll{
    display: none;
}
.home__scroll-button {
    color: $blue;
    transition: .3s;

    &:hover{
        transform: translateY(1rem);
    }
}

.home__scroll-mouse {
    font-size: 4rem;
}

.home__scroll-name {
    font-size: 1.5rem;
    color: $title-color;
    font-weight: $medium;
    margin-right: .5rem;
}

.home__scroll-arrow {
    font-size: 2rem;
}


