.button {
    display: inline-block;
    background-color: $blue;
    color: #fff;
    padding: 1.75rem;
    border-radius: 1rem;
    font-weight: $medium;

    &:hover {
        background-color: $blue-alt;
    }

    &__icon {
        font-size: 2rem;
        margin-left: .5rem;
        transition: .3s;
    }
}

.button--flex{
    display: inline-flex;
    align-items: center;
}



